@use "global" as *;

.sub-property-new__inner {
  position: relative;
  padding: rem(40) rem(80) 0;
  @include mq("md") {
    padding: vw(780, 64) vw(780, 48) 0;
  }
}

.sub-property-new__gallery {
  margin-top: rem(80);
  display: flex;
  gap: rem(150);
  @include mq("md") {
    margin-top: rem(53);
    flex-direction: column;
    gap: rem(20);
  }
}

.sub-property-new__image {
  max-width: rem(510);
  width: 100%;
  flex-shrink: 0;
  @include mq("md") {
    max-width: 100%;
  }
}

.sub-property-new__image img {
  aspect-ratio: 34 / 23;
  object-fit: cover;
  width: 100%;
}

.sub-property-new__content {
}

.sub-property-new__logo {
  max-width: rem(200);
  width: 100%;
  margin-top: rem(24);
}

.sub-property-new__text {
  margin-top: rem(22);
  margin-left: auto;
  max-width: rem(620);
  @include mq("md") {
    margin-top: vw(780, 48);
    font-size: vw(780, 26);
    margin-left: 0;
    max-width: 100%;
  }
}
