@use "global" as *;

.mouse-stalker {
  display: flex;
  align-items: center;
  justify-content: center;
  width: rem(96);
  height: rem(96);
  overflow: hidden;
  border: 1px solid $gold;
  background: linear-gradient(
    183deg,
    rgba(255, 255, 255, 0.2) 2.8%,
    rgba(255, 255, 255, 0.4) 136.71%
  );
  backdrop-filter: blur(rem(5));
  position: fixed;
  z-index: 9999;
  transform: translate(-75%, -75%) rotate(-45deg) scale(0);
  transition: transform 0.25s cubic-bezier(0.6, -0.28, 0.735, 0.045);
  pointer-events: none;
}

.mouse-stalker-active {
  transform: translate(-75%, -75%) rotate(-45deg) scale(1);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.mouse-stalker-text {
  transform: rotate(45deg);
  color: $black;
  text-align: center;
  font-family: $second-font-family;
  font-size: rem(14);
  font-weight: $bold;
  line-height: 1;
  white-space: nowrap;
}
