@use "global" as *;

.sub-environment {
  position: relative;
}

.sub-environment__inner {
  position: relative;
}

.sub-environment__link {
  margin-top: rem(48);
  @include mq("md") {
    margin-top: 0;
    flex: 1;
  }
}

.sub-environment__icon {
  display: block;
  position: relative;
  background-color: $black;
  width: rem(32);
  height: rem(32);
  @include mq("md") {
    width: vw(780, 64);
    height: vw(780, 64);
  }
}

.sub-environment__icon:after {
  position: absolute;
  background-image: url(../images/common/icon_blank.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  height: rem(16);
  width: rem(16);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  @include mq("md") {
    height: vw(780, 32);
    width: vw(780, 32);
  }
}

.sub-environment__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  @include mq("md") {
    align-items: center;
  }
}

.sub-environment__text-sub,
.sub-environment__text {
  display: block;
}

.sub-environment__text-sub {
  font-size: rem(16);
  @include mq("md") {
    font-size: vw(780, 20);
  }
}

.sub-environment__text {
  font-size: rem(23);
  font-weight: $bold;
  position: relative;
  display: inline-block;
  @include mq("md") {
    margin-top: vw(780, 16);
    font-size: vw(780, 36);
  }
}

.sub-environment__text::after {
  content: "";
  position: absolute;
  bottom: rem(-2);
  left: 0;
  width: 0;
  height: rem(1);
  background-color: $black;
  transition: width 0.3s ease;
}

.sub-environment__content {
  display: flex;
  @include mq("md") {
    flex-direction: column;
    padding: 0;
  }
}

.sub-environment__left,
.sub-environment__right {
  width: 50%;
  padding: rem(40) rem(40) rem(36);
  @include mq("md") {
    padding: vw(780, 64) vw(780, 48);
  }
}

.sub-environment__left {
  @include mq("md") {
    width: 100%;
    display: flex;
    align-items: center;
    gap: vw(780, 48);
  }
}

.sub-environment__right {
  position: relative;
  @include mq("md") {
    margin-left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    gap: vw(780, 48);
  }
}

.sub-environment__image {
  margin-left: auto;
  max-width: rem(220);
  overflow: hidden;
  @include mq("md") {
    max-width: vw(780, 148);
    margin-left: 0;
  }
}
.sub-environment__image img {
  width: 100%;
  aspect-ratio: 310/210;
  object-fit: cover;
  transition: transform 0.3s ease;
}

// environment__left と environment__right のホバーアニメーション
@media (hover: hover) {
  .sub-environment__left:hover .sub-environment__text::after,
  .sub-environment__right:hover .sub-environment__text::after {
    width: 100%;
  }

  .sub-environment__left:hover .sub-environment__image img,
  .sub-environment__right:hover .sub-environment__image img {
    transform: scale(1.05);
  }
}
