@use "global" as *;

.recruit__inner {
  padding: 0 rem(80);
  @include mq("md") {
    padding: 0 vw(780, 48);
  }
}

.recruit__wrap {
  display: flex;
  justify-content: space-between;
  @include mq("md") {
    flex-direction: column;
  }
}

.recruit__text {
  margin-top: rem(40);
  @include mq("md") {
    font-size: vw(780, 26);
    margin-top: vw(780, 80);
  }
}

.recruit__arrow-link {
  @include mq("md") {
    margin-top: vw(780, 80);
  }
}
