@use "global" as *;

.slider-gallery {
  display: flex;
  gap: rem(150);
  @include mq("md") {
    flex-direction: column;
    gap: rem(23);
  }
}

.slider-gallery__swiper {
  max-width: rem(510);
  width: 100%;
  position: relative;
  @include mq("md") {
    max-width: 100%;
  }
}

.slider-gallery__swiper .swiper {
  @include mq("md") {
    overflow: visible;
  }
}

.slider-gallery__wrap {
  position: relative;
  @include mq("md") {
    margin-right: vw(780, -48);
    height: vw(780, 380);
  }
}

.slider-gallery__swiper .swiper-slide {
  transform: translateX(-100%);
  transition: transform 0.8s ease-out;
  transition-delay: 0.5s;
  opacity: 1;
  @include mq("md") {
    transform: none;
    transition: none;
    height: vw(780, 380);
    margin-right: vw(780, 16);
  }
}

.slider-gallery__swiper .swiper-slide img {
  transform: translateX(100%);
  transition: transform 0.8s ease-out;
  transition-delay: 0.5s;
  width: 100%;
  aspect-ratio: 34/23;
  object-fit: cover;
  @include mq("md") {
    transform: none;
    transition: none;
    height: 100%;
  }
}

.slider-gallery__swiper .swiper-wrapper {
  @include mq("md") {
  }
}

/* アクティブスライドの追加スタイル */
.slider-gallery__swiper .swiper-slide-active {
  transform: translateX(0);
  @include mq("md") {
    transform: none;
  }
}

.slider-gallery__swiper .swiper-slide-active img {
  transform: translateX(0);
  @include mq("md") {
    transform: none;
  }
}

/* 非アクティブスライドは隠された状態を維持 */
.slider-gallery__swiper
  .swiper-slide:not(.swiper-slide-active):not(.is-animating) {
  transform: translateX(-100%);
  @include mq("md") {
    transform: none;
  }
}

.slider-gallery__swiper
  .swiper-slide:not(.swiper-slide-active):not(.is-animating)
  img {
  transform: translateX(100%);
  @include mq("md") {
    transform: none;
  }
}

.slider-gallery__content {
  @include mq("md") {
    margin-top: vw(780, 48);
  }
}

.slider-gallery__tab {
  display: flex;
  flex-wrap: wrap;
  gap: rem(5);
}

.slider-gallery__tab-title {
  font-size: rem(14);
  color: $white;
  background-color: $gold;
  padding: rem(5) rem(8);
  display: inline-block;
  width: fit-content;
  line-height: 1;
  text-align: center;
  font-weight: $normal;
  @include mq("md") {
    font-size: vw(780, 20);
    padding: vw(780, 12) vw(780, 16);
  }
}

.slider-gallery__logo {
  max-width: rem(200);
  width: 100%;
  margin-top: rem(24);
  @include mq("md") {
    max-width: vw(780, 298);
    margin-top: vw(780, 32);
  }
}

.slider-gallery__logo img {
  width: 100%;
  aspect-ratio: 399/64;
  object-fit: cover;
}

.slider-gallery__text {
  margin-top: rem(24);
  @include mq("md") {
    margin-top: vw(780, 48);
    font-size: vw(780, 26);
  }
}

.slider-gallery__text.slider-gallery__text--margin {
  @include mq("md") {
    margin-top: 0;
  }
}

/* slider-gallery Controls Wrapper */
.slider-gallery__controls {
  position: absolute;
  bottom: 0;
  right: rem(-100);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: rem(24);
  z-index: 10;
}

/* slider-gallery Pagination */
.slider-gallery__controls .swiper-pagination {
  position: static;
  width: auto;
  height: auto;
  text-align: right;
  z-index: 10;
  font-family: inherit;
  font-size: rem(14);
  font-weight: $medium;
  color: #333;
  line-height: 1;
}

.slider-gallery__controls .swiper-pagination .swiper-pagination-bullet {
  display: none;
}

/* slider-gallery Navigation */
.slider-gallery__controls .swiper-navigation .swiper-button-prev,
.slider-gallery__controls .swiper-navigation .swiper-button-next {
  position: static;
  width: rem(48);
  height: rem(48);
  margin: 0;
  background: #f3f3f3;
  border: none;
  border-radius: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  transform: none;
}

.slider-gallery__controls .swiper-navigation .swiper-button-prev {
  margin-top: rem(2);
}

.slider-gallery__controls .swiper-navigation .swiper-button-prev::after,
.slider-gallery__controls .swiper-navigation .swiper-button-next::after {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  transition: all 0.3s ease;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

/* 左矢印（<） */
.slider-gallery__controls .swiper-navigation .swiper-button-prev::after {
  transform: rotate(-135deg);
}

/* 右矢印（>） */
.slider-gallery__controls .swiper-navigation .swiper-button-next::after {
  transform: rotate(45deg);
}

/* ホバー時のアニメーション */
@media (hover: hover) {
  .slider-gallery__controls
    .swiper-navigation
    .swiper-button-prev:hover::after {
    animation: slider-gallery-arrow-bounce-left 0.5s ease-in-out forwards;
  }

  .slider-gallery__controls
    .swiper-navigation
    .swiper-button-next:hover::after {
    animation: slider-gallery-arrow-bounce-right 0.5s ease-in-out forwards;
  }
}

/* 左矢印のアニメーション（左にスライドして右から戻る） */
@keyframes slider-gallery-arrow-bounce-left {
  0% {
    transform: translateX(0) rotate(-135deg);
    opacity: 1;
  }
  50% {
    transform: translateX(-16px) rotate(-135deg);
    opacity: 0;
  }
  51% {
    transform: translateX(16px) rotate(-135deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(-135deg);
    opacity: 1;
  }
}

/* 右矢印のアニメーション（右にスライドして左から戻る） */
@keyframes slider-gallery-arrow-bounce-right {
  0% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
  50% {
    transform: translateX(16px) rotate(45deg);
    opacity: 0;
  }
  51% {
    transform: translateX(-16px) rotate(45deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
}
