@use "global" as *;

.line-visual {
  position: absolute;
  width: rem(700);
  height: rem(700);
  top: rem(-265);
  right: rem(-38);
  overflow: hidden;
  transform: rotate(45deg);

  @include mq("md") {
    width: vw(780, 650);
    height: vw(780, 650);
    top: vw(780, -230);
    right: rem(-73);
  }
}

.line-visual svg {
  width: 100%;
  height: 100%;
}

.line-visual svg path {
  fill: none;
  stroke: $gold;
  stroke-width: 0.15;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  transition: stroke-dashoffset 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955);
  // transition: stroke-dashoffset 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  @include mq("md") {
    // stroke-width: 0.1;
  }
}

.line-visual.is-animate svg path {
  stroke-dashoffset: 0;
}

.line-visual svg .bottom-line {
  transition-delay: 0.6s;
}

.line-visual svg .right-line {
  transition-delay: 0.8s;
}

.line-visual.line-visual--single {
  width: rem(1000);
  height: rem(1000);
  left: rem(-85);
  top: rem(-377);
  @include mq("md") {
    width: vw(780, 800);
    height: vw(780, 800);
    transform: rotate(-45deg);
    right: vw(780, -124);
    top: vw(780, -222);
    left: auto;
  }
}

.line-visual.line-visual--single svg {
  width: 100%;
  height: 100%;
}

.line-visual.line-visual--single svg path {
  stroke-width: 0.15;
}

.line-visual.line-visual--single svg .bottom-line {
  transition-delay: 0.6s;
  @include mq("md") {
    transition-delay: 0.8s;
  }
}

.line-visual.line-visual--single svg .right-line {
  transition-delay: 0.8s;
}

.line-visual.line-visual--single svg .top-line {
  transition-delay: 1s;
  @include mq("md") {
    transition-delay: 1s;
  }
}

.line-visual.line-visual--single svg .left-line {
  transition-delay: 1.8s;
  @include mq("md") {
    transition-delay: 0.8s;
  }
}
