@use "global" as *;

.sub-property__inner {
  position: relative;
  padding: rem(40) rem(80) 0;
  @include mq("md") {
    padding: vw(780, 64) vw(780, 48);
  }
}

.sub-property__gallery {
  margin-top: rem(80);
  @include mq("md") {
    margin-top: rem(53);
  }
}

.sub-property__gallery + .sub-property__gallery {
  margin-top: rem(48);
  @include mq("md") {
    margin-top: rem(40);
  }
}

.sub-property__text {
  margin-top: rem(22);
  margin-left: auto;
  max-width: rem(620);
  @include mq("md") {
    margin-top: vw(780, 64);
    margin-left: 0;
    font-size: vw(780, 26);
    max-width: 100%;
  }
}
