@use "global" as *;

.special__inner {
  padding: 0 rem(80);
  @include mq("md") {
    padding: 0 vw(780, 48);
  }
}

.special__wrap {
  display: flex;
  justify-content: space-between;
  align-items: end;
  @include mq("md") {
    flex-direction: column;
    align-items: start;
  }
}

.special__title {
}

.special__text {
  max-width: rem(480);
  @include mq("md") {
    max-width: 100%;
    margin-top: vw(780, 80);
    font-size: vw(780, 26);
  }
}

.special__gallery {
  margin-top: rem(98);
}

.special__content {
  margin-top: rem(64);
  display: block;
}

.special__image {
  position: relative;
  display: block;
  overflow: hidden;
}

.special__image img {
  width: 100%;
  aspect-ratio: 1425/380;
  object-fit: cover;
  transition: transform 0.3s ease;
  @include mq("md") {
    aspect-ratio: 780/360;
  }
}

.special__image:hover img {
  transform: scale(1.05);
}

.special__arrow-link {
  margin-top: rem(40);
  padding: 0 rem(80);
  @include mq("md") {
    margin-top: vw(780, 48);
    display: flex;
    padding: 0 vw(780, 48);
  }
}

.special__number {
  position: absolute;
  bottom: rem(-10);
  right: rem(80);
  font-size: rem(120);
  color: $white;
  font-weight: $medium;
  font-family: $second-font-family;
  line-height: 1;
  @include mq("md") {
    font-size: vw(780, 116);
    left: vw(780, 48);
  }
}

// special__content のホバーアニメーション
@media (hover: hover) {
  .special__content:hover .arrow-link__icon::after {
    animation: arrow-bounce 0.5s ease-in-out forwards;
  }

  .special__content:hover .arrow-link__text::after {
    width: 100%;
  }

  .special__content:hover .special__image img {
    transform: scale(1.05);
  }
}
