.arrow-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  color: #2c2826;
  font-size: 1rem;
  line-height: 1;
  transition: all 0.3s ease;
  position: relative;
}
@media screen and (max-width: 780px) {
  .arrow-link {
    font-size: 0.875rem;
  }
}

.arrow-link::before {
  content: "";
  position: absolute;
  width: 3rem;
  height: 3rem;
  background-color: #2c2826;
  z-index: -1;
  transition: all 0.3s ease;
}
@media screen and (max-width: 780px) {
  .arrow-link::before {
    width: 10.2564102564vw;
    height: 10.2564102564vw;
  }
}

@media screen and (max-width: 780px) {
  .arrow-link.arrow-link--small::before {
    width: 8.2051282051vw;
    height: 8.2051282051vw;
  }
}

@media screen and (max-width: 780px) {
  .arrow-link.arrow-link--environment::before {
    width: 6.1538461538vw;
    height: 6.1538461538vw;
  }
}

.arrow-link__icon {
  position: relative;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 780px) {
  .arrow-link__icon {
    width: 10.2564102564vw;
    height: 10.2564102564vw;
  }
}

@media screen and (max-width: 780px) {
  .arrow-link__icon.arrow-link__icon--small {
    width: 8.2051282051vw;
    height: 8.2051282051vw;
  }
}

@media screen and (max-width: 780px) {
  .arrow-link__icon.arrow-link__icon--environment {
    width: 6.1538461538vw;
    height: 6.1538461538vw;
  }
}

.arrow-link__icon::after {
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  border-right: 0.125rem solid #fff;
  border-top: 0.125rem solid #fff;
  transform: rotate(45deg);
  transition: transform 0.3s ease;
}
@media screen and (max-width: 780px) {
  .arrow-link__icon::after {
    width: 1.5384615385vw;
    height: 1.5384615385vw;
  }
}

.arrow-link__text {
  margin-left: 1.5rem;
  position: relative;
  font-weight: 700;
}
@media screen and (max-width: 780px) {
  .arrow-link__text {
    font-size: 3.3333333333vw;
    margin-left: 4.1025641026vw;
  }
}

.arrow-link__text.arrow-link__text--large {
  font-size: 1.4375rem;
}
@media screen and (max-width: 780px) {
  .arrow-link__text.arrow-link__text--large {
    font-size: 4.6153846154vw;
  }
}

@media screen and (max-width: 780px) {
  .arrow-link__text.arrow-link__text--environment {
    font-size: 3.8461538462vw;
  }
}

.arrow-link__text small {
  font-size: 0.8125rem;
}
@media screen and (max-width: 780px) {
  .arrow-link__text small {
    font-size: 2.5641025641vw;
  }
}

.arrow-link__text::after {
  content: "";
  position: absolute;
  bottom: -0.125rem;
  left: 0;
  width: 0;
  height: 0.0625rem;
  background-color: #2c2826;
  transition: width 0.3s ease;
}

.arrow-link__text.arrow-link__text--large::after,
.arrow-link__text.arrow-link__text--environment::after {
  bottom: -0.5rem;
}

@media (hover: hover) {
  .arrow-link:hover .arrow-link__icon::after {
    animation: arrow-bounce 0.5s ease-in-out forwards;
  }
  .arrow-link:hover .arrow-link__text::after {
    width: 100%;
  }
}
@keyframes arrow-bounce {
  0% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
  50% {
    transform: translateX(1rem) rotate(45deg);
    opacity: 0;
  }
  51% {
    transform: translateX(-1rem) rotate(45deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
}
.arrow-link.arrow-link--white {
