@use "global" as *;

.layout-entry-banner {
  margin-top: rem(88);
  @include mq("md") {
    margin-top: vw(780, 132);
  }
}

.layout-entry-banner.layout-entry-banner--sub {
  margin-top: rem(0);
}

.layout-entry-banner.layout-entry-banner--single {
  margin-top: 0;
  @include mq("md") {
    margin-top: vw(780, 60);
  }
}
