@use "global" as *;

// ===== 垂直線アニメーション =====

// 垂直線（基本）
.line-vertical {
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #cbc5c0;
  z-index: 1;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 1.4s cubic-bezier(0.19, 1, 0.22, 1);
}

// 垂直線（中央）
.line-vertical.line-vertical--center {
  left: 50%;
}

// アニメーション実行
.is-show .line-vertical {
  transform: scaleY(1);
}
