@use "global" as *;

.environment {
  position: relative;
}

.environment__inner {
  position: relative;
}

.environment__border-top,
.environment__border-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: $gold;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
}

.environment__border-bottom {
  top: auto;
  bottom: 0;
}

.environment__wrap-border {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: $gold;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
  transition-delay: 0.2s;
}

// アニメーション実行時の状態
.environment__inner.is-show .environment__border-top,
.environment__inner.is-show .environment__border-bottom {
  transform: scaleX(1);
}

.environment__inner.is-show .environment__wrap-border {
  transform: scaleX(1);
  position: relative;
}

.environment__inner.is-show .environment__wrap-line {
  transform: scaleX(1);
}

.environment__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: rem(96) rem(80) rem(102);
  @include mq("md") {
    align-items: start;
    flex-direction: column;
    padding: vw(780, 128) vw(780, 48) vw(780, 116);
  }
}

.environment__text {
  max-width: rem(480);
  @include mq("md") {
    max-width: 100%;
    font-size: vw(780, 26);
    margin-top: vw(780, 80);
  }
}

.environment__content {
  display: flex;
  position: relative;
  @include mq("md") {
    flex-direction: column;
    padding: 0;
  }
}

.environment__left {
  width: 50%;
  padding: rem(48) rem(48) rem(40);
  @include mq("md") {
    width: 100%;
    padding: vw(780, 66) vw(780, 48) vw(780, 54);
  }
}

.environment__right {
  position: relative;
  width: 50%;
  padding: rem(48) rem(48) rem(40);
  @include mq("md") {
    width: 100%;
    padding: vw(780, 66) vw(780, 48) vw(780, 54);
    margin-left: 0;
  }
}

.environment__image {
  margin-left: auto;
  max-width: rem(310);
  overflow: hidden;
  @include mq("md") {
    max-width: vw(780, 340);
  }
}
.environment__image img {
  width: 100%;
  aspect-ratio: 310/210;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.environment__arrow-link {
  margin-top: rem(106);
  @include mq("md") {
    margin-top: vw(780, 48);
  }
}

// environment__left と environment__right のホバーアニメーション
@media (hover: hover) {
  .environment__left:hover .arrow-link__icon::after,
  .environment__right:hover .arrow-link__icon::after {
    animation: arrow-bounce 0.5s ease-in-out forwards;
  }

  .environment__left:hover .arrow-link__text::after,
  .environment__right:hover .arrow-link__text::after {
    width: 100%;
  }

  .environment__left:hover .environment__image img,
  .environment__right:hover .environment__image img {
    transform: scale(1.05);
  }
}

// 往復する矢印のアニメーション（arrow-linkから移植）
@keyframes arrow-bounce {
  0% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
  50% {
    transform: translateX(rem(16)) rotate(45deg);
    opacity: 0;
  }
  51% {
    transform: translateX(rem(-16)) rotate(45deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
}

// 遅延アニメーションの個別設定
.environment__wrap-line {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: $gold;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
  transition-delay: 0.2s; // 上下の線の後に表示
}

.environment__content-line {
  transition-delay: 0.4s;
}

.environment__vertical-line {
  transition-delay: 0.6s;
}
