@use "global" as *;

.sp-buttons {
  display: none;

  @media (max-width: 780px) {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    z-index: 1005;
    font-size: rem(28);
    transition: transform 0.3s ease;
  }
}

.sp-buttons__entry,
.sp-buttons__menu {
  @include mq("md") {
    width: 50%;
    color: $white;
    text-align: center;
    font-weight: $medium;
    font-size: vw(780, 28);
    font-family: $second-font-family;
    padding: vw(780, 40) 0;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1.7;
    text-transform: uppercase;
  }
}

.sp-buttons__entry {
  background-color: $gold;
}

.sp-buttons__menu {
  background-color: $black;
  gap: rem(16);
  transition: all 0.3s ease;
}

.sp-buttons__menu-text {
  display: inline-block;
}

.sp-buttons__menu-icon {
  position: relative;
  width: vw(780, 22);
  height: vw(780, 22);
  display: inline-block;
}

.sp-buttons__menu-icon span {
  position: absolute;
  height: vw(780, 4);
  width: 100%;
  background: $gold;
  left: 0;
  transition: all 0.3s ease;
  transform-origin: center;
}

.sp-buttons__menu-icon span:nth-child(1) {
  top: 0;
}

.sp-buttons__menu-icon span:nth-child(2) {
  top: vw(780, 8);
}

.sp-buttons__menu-icon span:nth-child(3) {
  top: vw(780, 16);
}

.sp-buttons__menu.is-open .sp-buttons__line:nth-child(1) {
  transform: rotate(45deg);
  top: vw(780, 8);
}

.sp-buttons__menu.is-open .sp-buttons__line:nth-child(2) {
  opacity: 0;
}

.sp-buttons__menu.is-open .sp-buttons__line:nth-child(3) {
  transform: rotate(-45deg);
  top: vw(780, 8);
}

@media screen and (max-width: 780px) {
  .sp-buttons.is-hidden {
    transform: translateY(100%);
  }
}
