@use "global" as *;

.people-slider {
  margin-top: rem(50);
  position: relative;
  height: rem(594);
  @include mq("md") {
    margin-top: vw(780, 96);
    height: vw(780, 750);
  }
}

.people-slider .swiper {
  max-width: rem(290);
  overflow: visible !important;
  transform: translateX(calc((100vw - rem(160)) / 2 * -1 + rem(145)));
  @include mq("md") {
    max-width: vw(780, 360);
    transform: translateX(-21vw);
  }
}

.people-slider .swiper-slide {
  width: rem(290);
  height: auto;
  transition: all 0.3s ease;
  @include mq("md") {
    width: vw(780, 360);
    height: vw(780, 434);
  }
}

.people-slider:not(.people-slider-single) .swiper-slide-active {
  width: rem(400) !important;
  height: rem(481) !important;
  @include mq("md") {
    width: vw(780, 506) !important;
    height: vw(780, 608) !important;
  }
}

.people-slider:not(.people-slider-single) .swiper-slide-active.is-break {
  width: rem(290) !important;
  height: auto !important;
  @include mq("md") {
    width: vw(780, 360) !important;
    height: auto !important;
  }
}

.people-slider:not(.people-slider-single) .swiper-slide-active:not(.is-break) {
  width: rem(400) !important;
  height: rem(481) !important;
  @include mq("md") {
    width: vw(780, 506) !important;
    height: vw(780, 608) !important;
  }
}

.people-slider .card__content {
  @include mq("md") {
    transform: scaleY(0) translateY(vw(780, 8));
    -webkit-transform-origin: left bottom;
  }
}

.people-slider:not(.people-slider-single)
  .swiper-slide-active:not(.is-break)
  .card__content {
  @include mq("md") {
    transform: scaleY(1) translateY(vw(780, 8));
  }
}

.people-slider:not(.people-slider-single)
  .swiper-slide-active:not(.is-break)
  .card__content {
  transition: transform 0.3s 0.3s;
}

.people-slider:not(.people-slider-single)
  .swiper-slide-active:not(.is-break)
  .card__text {
  opacity: 1;
  transition: opacity 0.3s 0.6s;
}

.people-slider:not(.people-slider-single) .swiper-slide:hover {
  width: rem(400) !important;
  height: rem(481) !important;
  @include mq("md") {
    width: vw(780, 506) !important;
    height: vw(780, 608) !important;
  }
}

.people-slider .people-slider__controls {
  position: absolute;
  top: rem(-115);
  right: rem(80);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: rem(24);
  z-index: 10;

  @include mq("md") {
    justify-content: space-between;
    top: auto;
    bottom: 0;
    right: auto;
    left: vw(780, 48);
    width: vw(780, 506);
  }
}

.people-slider .swiper-wrapper {
  display: flex;
  @include mq("md") {
    overflow: visible !important;
  }
}

.people-slider__controls .swiper-pagination {
  position: static;
  width: auto;
  height: rem(40);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2c2826;
  text-align: right;
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-weight: 500;
  font-size: rem(14);
  line-height: 1;
  pointer-events: none;
  user-select: none;
  z-index: 10;
  letter-spacing: 1px;

  @include mq("md") {
    font-size: vw(780, 28);
  }
}

.people-slider__controls .swiper-navigation {
  display: flex;
  gap: rem(16);
  @include mq("md") {
    gap: vw(780, 4);
  }
}

.people-slider__controls .swiper-pagination .swiper-pagination-bullet {
  display: none;
}

.people-slider__controls .swiper-navigation .swiper-button-prev,
.people-slider__controls .swiper-navigation .swiper-button-next {
  position: static;
  width: rem(40);
  height: rem(40);
  margin: 0;
  background: #f3f3f3;
  border: none;
  border-radius: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  transform: none;
  @include mq("md") {
    width: vw(780, 80);
    height: vw(780, 80);
  }
}

.people-slider__controls .swiper-navigation .swiper-button-prev::after,
.people-slider__controls .swiper-navigation .swiper-button-next::after {
  content: "";
  width: rem(10);
  height: rem(10);
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  transition: all 0.3s ease;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  @include mq("md") {
    width: vw(780, 12);
    height: vw(780, 12);
  }
}

/* 左矢印（<） */
.people-slider__controls .swiper-navigation .swiper-button-prev::after {
  transform: rotate(-135deg);
}

/* 右矢印（>） */
.people-slider__controls .swiper-navigation .swiper-button-next::after {
  transform: rotate(45deg);
}

/* 左矢印のアニメーション（左にスライドして右から戻る） */
@keyframes people-arrow-bounce-left {
  0% {
    transform: translateX(0) rotate(-135deg);
    opacity: 1;
  }
  50% {
    transform: translateX(-16px) rotate(-135deg);
    opacity: 0;
  }
  51% {
    transform: translateX(16px) rotate(-135deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(-135deg);
    opacity: 1;
  }
}

/* 右矢印のアニメーション（右にスライドして左から戻る） */
@keyframes people-arrow-bounce-right {
  0% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
  50% {
    transform: translateX(16px) rotate(45deg);
    opacity: 0;
  }
  51% {
    transform: translateX(-16px) rotate(45deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
}

/* ホバー時のアニメーション */
@media (hover: hover) {
  .people-slider__controls .swiper-navigation .swiper-button-prev:hover::after {
    animation: people-arrow-bounce-left 0.5s ease-in-out forwards;
  }

  .people-slider__controls .swiper-navigation .swiper-button-next:hover::after {
    animation: people-arrow-bounce-right 0.5s ease-in-out forwards;
  }
}

/* 無効状態の処理 */
.people-slider__controls .swiper-navigation .swiper-button-disabled {
  opacity: 0.35;
  cursor: not-allowed;
  pointer-events: none;
}
