@use "global" as *;

.arrow-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  color: $black;
  font-size: rem(16);
  line-height: 1;
  transition: all 0.3s ease;
  position: relative;
  @include mq("md") {
    font-size: rem(14);
  }
}

.arrow-link::before {
  content: "";
  position: absolute;
  width: rem(48);
  height: rem(48);
  background-color: $black;
  z-index: -1;
  transition: all 0.3s ease;
  @include mq("md") {
    width: vw(780, 80);
    height: vw(780, 80);
  }
}

.arrow-link.arrow-link--small::before {
  @include mq("md") {
    width: vw(780, 64);
    height: vw(780, 64);
  }
}

.arrow-link.arrow-link--environment::before {
  @include mq("md") {
    width: vw(780, 48);
    height: vw(780, 48);
  }
}

.arrow-link__icon {
  position: relative;
  width: rem(48);
  height: rem(48);
  display: flex;
  align-items: center;
  justify-content: center;
  @include mq("md") {
    width: vw(780, 80);
    height: vw(780, 80);
  }
}

.arrow-link__icon.arrow-link__icon--small {
  @include mq("md") {
    width: vw(780, 64);
    height: vw(780, 64);
  }
}

.arrow-link__icon.arrow-link__icon--environment {
  @include mq("md") {
    width: vw(780, 48);
    height: vw(780, 48);
  }
}

.arrow-link__icon::after {
  content: "";
  width: rem(10);
  height: rem(10);
  border-right: rem(2) solid $white;
  border-top: rem(2) solid $white;
  transform: rotate(45deg);
  transition: transform 0.3s ease;
  @include mq("md") {
    width: vw(780, 12);
    height: vw(780, 12);
  }
}

.arrow-link__text {
  margin-left: rem(24);
  position: relative;
  font-weight: $bold;
  @include mq("md") {
    font-size: vw(780, 26);
    margin-left: vw(780, 32);
  }
}

.arrow-link__text.arrow-link__text--large {
  font-size: rem(23);
  @include mq("md") {
    font-size: vw(780, 36);
  }
}

.arrow-link__text.arrow-link__text--environment {
  @include mq("md") {
    font-size: vw(780, 30);
  }
}

.arrow-link__text small {
  font-size: rem(13);
  @include mq("md") {
    font-size: vw(780, 20);
  }
}

.arrow-link__text::after {
  content: "";
  position: absolute;
  bottom: rem(-2);
  left: 0;
  width: 0;
  height: rem(1);
  background-color: $black;
  transition: width 0.3s ease;
}

.arrow-link__text.arrow-link__text--large::after,
.arrow-link__text.arrow-link__text--environment::after {
  bottom: rem(-8);
}

// Hover animation
@media (hover: hover) {
  .arrow-link:hover .arrow-link__icon::after {
    animation: arrow-bounce 0.5s ease-in-out forwards;
  }

  .arrow-link:hover .arrow-link__text::after {
    width: 100%;
  }
}

// 往復する矢印のアニメーション
@keyframes arrow-bounce {
  0% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
  50% {
    transform: translateX(rem(16)) rotate(45deg);
    opacity: 0;
  }
  51% {
    transform: translateX(rem(-16)) rotate(45deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(45deg);
    opacity: 1;
  }
}

.arrow-link.arrow-link--white {
  color: $white;
}

.arrow-link.arrow-link--white::before {
  background-color: $white;
}

.arrow-link.arrow-link--white .arrow-link__icon::after {
  border-color: $black;
}

.arrow-link.arrow-link--white .arrow-link__text::after {
  background-color: $white;
}
