@use "global" as *;

.entry-button__link {
  font-size: rem(14);
  font-family: $second-font-family;
  font-weight: $medium;
  line-height: 1;
  color: $white;
  background: linear-gradient(to right, #000, #b89d68);
  padding: rem(13) 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  transition: all 0.3s ease;
  overflow: hidden;
  width: rem(120);
}

.entry-button__link::after {
  content: "";
  position: absolute;
  top: -50%;
  left: 100%;
  width: 300%;
  height: 200%;
  background: #b89d68;
  filter: blur(rem(20));
  opacity: 0;
  transition: all 0.3s ease;
  z-index: -1;
}

.entry-button__link:hover::after {
  left: -100%;
  opacity: 1;
}

.entry-button__link.entry-button__link--drawer {
  @media (max-width: 1240px) {
    padding: rem(32) 0;
    width: 100%;
    font-size: rem(20);
  }
}
