@use "global" as *;

.drawer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(100, 100, 100, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
  z-index: 1000;
  @media (max-width: 780px) {
    display: none;
  }
}

.drawer-overlay.is-open {
  opacity: 1;
  visibility: visible;
}
