@use "global" as *;

.main-mv {
  position: relative;
}

.main-mv__inner {
  padding-inline: rem(80);
  @include mq("md") {
    padding-inline: vw(780, 48);
  }
}

.main-mv__title {
  overflow: hidden;
  padding: rem(120) 0 rem(84);
  @include mq("md") {
    padding: vw(780, 64) 0 vw(780, 160);
  }
}

.main-mv__visual {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  left: 0;
  opacity: 0;
  position: sticky;
  top: 0;
  transition: opacity 1s ease;
  width: 100%;
}

.main-mv__visual.is-show {
  opacity: 1;
  transition-delay: 1s;
}

.main-mv__image {
  -webkit-clip-path: inset(10% 25% 10% 25%);
  clip-path: inset(10% 25% 10% 25%);
  height: 100%;
  left: 0;
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  top: 0;
  transition: transform 0.2s, -webkit-clip-path 0.2s;
  transition: clip-path 0.2s, transform 0.2s;
  transition: clip-path 0.2s, transform 0.2s, -webkit-clip-path 0.2s;
  width: 100%;
  z-index: 1;
}

.main-mv__scene,
.main-mv__shade {
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  transition: opacity 0.5s ease;
}

.main-mv__scene--1 {
  opacity: 1;
}

// 最初の画像用（アニメーションなし）
.main-mv__scene.is-initial {
  opacity: 1;
  z-index: 3;
}

// 2回目以降の切り替え用（アニメーションあり）
.main-mv__scene.is-active {
  opacity: 1;
  z-index: 3;
  animation: main-mv-mask-animation 0.5s linear;
}

.main-mv__shade {
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 0.5s ease;
  z-index: 4;
  opacity: 0;
}

.main-mv__scene.is-previous {
  opacity: 1;
  z-index: 2;
}

.main-mv__scene img,
.main-mv__shade img {
  -o-object-fit: cover;
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.main-mv__text {
  color: #fff;
  display: flex;
  flex-direction: column;
  z-index: 3;
}

.main-mv__message {
  font-size: rem(20);
  font-weight: 700;
  line-height: 1.7;
  letter-spacing: 0.01em;
  @include mq("md") {
    font-size: vw(780, 32);
  }
}

.main-mv__message span {
  display: inline-block;
  opacity: 0;
  transform: translateY(20%);
  transition-property: opacity, transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.main-mv__message.is-show span {
  opacity: 1;
  transform: translateY(0);
}

.main-mv__link {
  margin-top: rem(40);
  opacity: 0;
  pointer-events: none;
  transform: translateY(20%);
  transition-delay: 1s;
  transition-duration: 0.6s;
  transition-property: opacity, transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.main-mv__link.is-active {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0);
}

.main-mv__height-block {
  width: 10px;
  height: 2000px;
}

// main-mv用のページめくり効果（0.5秒）
@keyframes main-mv-mask-animation {
  0% {
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  }
  75% {
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 30% 100%);
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
