@use "global" as *;

.single-blog__inner {
  padding-left: rem(80);
  @include mq("md") {
    padding-inline: vw(780, 48);
  }
}

.single-blog__mv {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include mq("md") {
    flex-direction: column;
  }
}

.single-blog__title {
  font-size: rem(32);
  font-weight: $bold;
  text-align: left;
}

.single-blog__text {
  margin-top: rem(40);
  text-align: left;
  @include mq("md") {
    font-size: vw(780, 20);
    margin-top: vw(780, 32);
  }
}

.single-blog__image {
  position: relative;
  width: calc(100% - rem(740));
  min-width: calc((100% - rem(40)) / 2);
  @include mq("md") {
    margin-top: vw(780, 72);
    margin-right: vw(780, -48);
    width: calc(100% + vw(780, 48));
    height: vw(780, 500);
  }
}

.single-blog__image img {
  width: 100%;
  aspect-ratio: 35/24;
  object-fit: cover;
}

.single-blog__content-wrap {
  display: flex;
  justify-content: space-between;
  margin: rem(96) auto 0;
  padding: rem(40) 0 0;
  border-top: 1px solid #cbc5c0;
  width: calc(100% - rem(160));
  @include mq("md") {
    flex-direction: column;
    margin: vw(780, 128) auto 0;
    padding: vw(780, 44) 0 0;
    width: vw(780, 684);
  }
}

.single-blog__content-wrap.single-blog__content-wrap--margin {
  margin-top: rem(160);
  @include mq("md") {
    margin-top: vw(780, 128);
  }
}

.single-blog__content-title {
  font-size: rem(23);
  flex-shrink: 0;
  @include mq("md") {
    font-size: vw(780, 36);
  }
}

.single-blog__content {
  width: 100%;
  max-width: calc(100% - rem(440));
  @include mq("md") {
    max-width: 100%;
    margin-top: vw(780, 92);
  }
}

.single-blog__content-catch {
  font-size: rem(20);
  line-height: 1.5;
  letter-spacing: 0.01em;
  @include mq("md") {
    font-size: vw(780, 32);
  }
}

.single-blog__content-catch:not(:first-of-type) {
  margin-top: rem(40);
}

.single-blog__content-text {
  margin-top: rem(32);
  font-weight: $medium;
  line-height: 1.7;
  @include mq("md") {
    font-size: vw(780, 26);
    margin-top: vw(780, 32);
  }
}

.single-blog__img-wrap {
  margin: rem(94) auto 0;
  width: calc(100% - rem(160));
  height: rem(450);
  @include mq("md") {
    margin: vw(780, 96) auto 0;
    width: vw(780, 684);
    height: vw(780, 438);
  }
}

.single-blog__img {
  height: 100%;
  @include mq("md") {
    max-width: vw(780, 684);
  }
}

.single-blog__img img {
  width: 100%;
  aspect-ratio: 114/73;
  object-fit: cover;
}

.single-blog__timetable-wrap {
  margin: rem(96) auto 0;
  max-width: rem(840);
  padding: rem(64);
  display: flex;
  gap: rem(80);
  justify-content: center;
  align-items: center;
  border-left: 1px solid #cbc5c0;
  background-color: #f3f3f3;
  @include mq("md") {
    flex-direction: column;
    align-items: flex-start;
    gap: vw(780, 64);
    margin: vw(780, 128) vw(780, 48) 0;
    padding: vw(780, 128) vw(780, 48) vw(780, 120);
  }
}

.single-blog__timetable-title {
  font-size: rem(20);
  font-weight: $bold;
  letter-spacing: 0.08em;
  @include mq("md") {
    font-size: vw(780, 32);
  }
}

.single-blog__timetable {
  display: flex;
  flex-direction: column;
  gap: rem(8);
  @include mq("md") {
    gap: vw(780, 16);
  }
}

.single-blog__timetable-item {
  display: flex;
  align-items: center;
  @include mq("md") {
    align-items: flex-start;
  }
}

.single-blog__timetable-time {
  font-size: rem(20);
  min-width: rem(110);
  flex-shrink: 0;
  @include mq("md") {
    font-size: vw(780, 32);
    min-width: vw(780, 110);
  }
}

.single-blog__timetable-time span {
  font-family: $second-font-family;
  display: inline-block;
  background: linear-gradient(123deg, #2c2826 36.16%, #b99568 93.47%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.single-blog__timetable-text {
  max-width: rem(318);
  font-weight: $normal;
  letter-spacing: 0.08em;
  line-height: 1.7;
  margin-left: rem(20);
  @include mq("md") {
    font-size: vw(780, 26);
    max-width: 100%;
    margin-left: vw(780, 20);
  }
}

.single-blog__slider {
  margin-top: rem(104);
  padding: rem(80);
  border-top: 1px solid #cbc5c0;
  @include mq("md") {
    padding: vw(780, 128) vw(780, 48) vw(780, 120);
  }
}
