@use "global" as *;

.card {
  transition: 0.45s ease;
  position: relative;
  display: block;
  text-decoration: none;
  color: inherit;
  flex-shrink: 0;
}

.card__image {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.card__image img {
  aspect-ratio: 323/388;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.card__content {
  background-color: $white;
  font-weight: 400;
  line-height: 170%;
  position: absolute;
  left: 0;
  bottom: 0;
  margin-left: -1px;
  transform: scaleY(1) translateY(4px);
  transform-origin: left bottom;
  padding: rem(16) rem(20) rem(0) rem(1);
  font-size: rem(14);
  text-align: left;
  @include mq("md") {
    padding: vw(780, 36) vw(780, 48) vw(780, 0) vw(780, 1);
    font-size: vw(780, 20);
    transform-origin: left bottom;
  }
}

.card__text {
  opacity: 0;
}

.card:hover .card__content {
  transform: scaleY(1) translateY(4px);
  @include mq("md") {
  }
}

.card:hover .card__text {
  opacity: 1;
  transition: opacity 0.3s 0.3s ease;
}
