@use "global" as *;

.entry-banner {
  position: relative;
  height: rem(320);
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  @include mq("md") {
    height: vw(780, 600);
  }
}

.entry-banner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #cbc5c0;
  z-index: 10;
}

.entry-banner__img-wrapper {
  position: absolute;
  top: -10vh;
  left: 0;
  width: 100%;
  height: calc(100% + 20vh);
  z-index: 0;
  overflow: hidden;
}

.entry-banner__img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: translateY(0);
  will-change: transform;
}

.entry-banner__inner {
  position: relative;
  z-index: 2;
  text-align: center;
}

.entry-banner__diamond {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: rem(520);
  height: rem(520);
  background: transparent;
  border: 1px solid $gold;

  @include mq(md) {
    width: vw(780, 640);
    height: vw(780, 640);
  }
}

.entry-banner__text {
  color: $black;
  font-weight: $medium;
  line-height: 1;
  margin-top: rem(8);
  @include mq(md) {
    font-size: vw(780, 26);
    margin-top: vw(780, 16);
  }
}
