@use "global" as *;

.title__sub {
  font-size: rem(14);
  font-weight: $medium;
  text-transform: uppercase;
  line-height: 1;
  color: $black;
  font-family: $second-font-family;
  background: linear-gradient(
    153deg,
    #2c2826 50%,
    #2c2826 72.32%,
    #b99568 96.735%
  );
  background-size: 200% 200%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;

  @include mq("md") {
    font-size: vw(780, 22);
  }
}

.title__main {
  margin-top: rem(16);
  font-size: rem(32);
  display: inline-block;
  line-height: 1;
  background: linear-gradient(
    123deg,
    #2c2826 50%,
    #2c2826 72.32%,
    #b99568 96.735%
  );
  background-size: 200% 200%;
  background-position: 0 0;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;

  @include mq("md") {
    font-size: vw(780, 56);
    margin-top: vw(780, 30);
  }
}

.title__main.title__main--entry {
  margin-top: 0;
  font-size: rem(42);
  font-family: $second-font-family;
  font-weight: $medium;
  line-height: 1;
  @include mq("md") {
    font-size: vw(780, 68);
  }
}

.title__main.title__main--small {
  font-size: rem(28);
  margin-top: 0;
  @include mq("md") {
    font-size: vw(780, 48);
  }
}

.title.is-show .title__sub {
  animation: title-transition 0.6s 0s cubic-bezier(0.215, 0.61, 0.355, 1)
      forwards,
    title-gradient 0.4s 1s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  opacity: 1;
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
}

.title.is-show .title__main {
  animation: title-transition 0.6s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1)
      forwards,
    title-gradient 0.4s 1s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
  opacity: 1;
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
}

.title__main.title__main--catch1,
.title__main.title__main--catch2 {
  height: auto;
  background-color: #2c2826;
  mask-size: contain;
  mask-repeat: no-repeat;
  position: relative;
  transform: translateY(12.5%);
  transition: transform 1.2s 0.2s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 0;
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  background-clip: border-box;
}

.title__main.title__main--catch1 span,
.title__main.title__main--catch2 span {
  display: none;
}

.title__main.title__main--catch1::before,
.title__main.title__main--catch2::before {
  content: "";
  display: block;
  width: rem(1794);
  height: rem(506);
  top: rem(-113);
  left: rem(-70);
  background-image: url(../images/common/gold-back.webp);
  background-size: contain;
  background-position: left top;
  position: absolute;
  opacity: 0;
  transform: translate(-100%, 0);
  transition: transform 2.4s 0.6s cubic-bezier(0.19, 1, 0.22, 1),
    opacity 0.1s 0.6s linear;
  @include mq("md") {
    width: vw(780, 1516);
    height: vw(780, 428);
    top: vw(780, -96);
    left: vw(780, -58);
  }
}

.title__main.title__main--catch1 {
  aspect-ratio: 459.1/135.4;
  width: rem(459.5);
  mask-image: url(../images/common/mv_title1.svg);
  @include mq("md") {
    width: vw(780, 386);
  }
}

.title__main.title__main--catch2 {
  aspect-ratio: 744.7/138.1;
  mask-image: url(../images/common/mv_title2.svg);
  margin-top: rem(32);
  width: rem(743);
  @include mq("md") {
    margin-top: vw(780, 26);
    width: vw(780, 630);
  }
}

.title.is-show .title__main.title__main--catch1,
.title.is-show .title__main.title__main--catch2 {
  transform: translateY(0);
  opacity: 1;
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  animation: title-transition 0.6s 0s cubic-bezier(0.215, 0.61, 0.355, 1)
      forwards,
    title-gradient 0.4s 1s cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
}

.title.is-show .title__main.title__main--catch1::before,
.title.is-show .title__main.title__main--catch2::before {
  opacity: 1;
  transform: translate(0, 0);
}

@keyframes title-transition {
  0% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  }
  90% {
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 100%);
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

@keyframes title-gradient {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 100%;
  }
}
