@use "global" as *;

// 水平線（基本）
.line-horizontal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #cbc5c0;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
}

// 水平線（下部）
.line-horizontal.line-horizontal--bottom {
  top: auto;
  bottom: 0;
}

// アニメーション実行
.is-show .line-horizontal {
  transform: scaleX(1);
}
