@use "global" as *;

.special-loop {
  overflow: hidden;
  position: relative;
}

.special-loop__inner {
  width: 100%;
  position: relative;
}

.special-loop__list {
  display: flex;
  height: rem(168);
  animation: slideLoop 30s linear infinite;
  width: max-content;
  @include mq("md") {
    height: vw(780, 228);
  }
}

@keyframes slideLoop {
  100% {
    transform: translateX(-50%);
  }
}

.special-loop__item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: rem(32);
  min-width: rem(300);
  flex-shrink: 0;
  margin-left: rem(30);
}

.special-loop__img {
  max-width: rem(118);
  width: 100%;
  @include mq("md") {
    max-width: vw(780, 177);
  }
}

.special-loop__img img {
  width: 100%;
  aspect-ratio: 118/80;
  object-fit: cover;
}

.special-loop__text {
  font-size: rem(86);
  font-weight: $medium;
  line-height: 1;
  font-family: $second-font-family;
  text-transform: uppercase;
  background: linear-gradient(123deg, #2c2826 36.16%, #b99568 93.47%);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include mq("md") {
    font-size: vw(780, 80);
  }
}
