@use "global" as *;

.header {
  height: rem(60);
  @include mq("md") {
    height: vw(780, 60);
  }
}

.header.is-hidden {
  top: -110%;
}

.header__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: inherit;
  padding-inline: rem(80);
  @include mq("lg") {
    padding-inline: vw(780, 48);
  }
}

.header__logo {
  max-width: rem(145);
  width: 100%;
  height: inherit;

  @include mq("md") {
    max-width: vw(780, 216);
  }
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
  gap: rem(8);
  @include mq("md") {
    gap: vw(780, 32);
  }
}

.header__logo img {
  width: 100%;
  aspect-ratio: 146/32;
  object-fit: cover;
  flex-shrink: 0;
}

.header__logo-text {
  font-size: rem(12);
  font-weight: 500;
  line-height: 1.2;
  color: $black;
  white-space: nowrap;
  @include mq("md") {
    font-size: vw(780, 22);
  }
}

.header__nav {
  position: relative;
  height: inherit;
  display: block;
  @media (max-width: 1240px) {
    display: none;
  }
}

.header__nav-list {
  display: flex;
  gap: rem(24);
  height: inherit;
}

.header__nav-item {
  position: relative;
  font-size: rem(14);
  font-weight: 500;
  color: $black;
  letter-spacing: 0.05em;
  height: inherit;
}

.header__nav-item.header__nav-item--entry {
  height: auto;
  display: flex;
  align-items: center;
}

.header__nav-link {
  height: inherit;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.header__nav-link:hover {
  color: $gold;
}

.header__nav-item--has-sub:hover .header__sublist,
.header__sublist:hover {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  pointer-events: auto;
}

.header__nav-item--has-sub:hover > .header__nav-link {
  color: $gold;
}

.header__nav-item--has-sub {
  position: relative;
  display: inline-block;
}

.header__nav-link--has-sub {
  position: relative;
}

.header__nav-link--has-sub::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: rem(-2);
  transform: translateX(-50%) scaleX(0);
  transform-origin: center;
  width: 100%;
  height: rem(1);
  background: $gold;
  transition: transform 0.3s ease;
  z-index: 11;
}

.header__nav-item--has-sub:hover .header__nav-link--has-sub::after {
  transform: translateX(-50%) scaleX(1);
}

.header__icon {
  margin-left: rem(6);
  font-weight: bold;
  font-size: rem(14);
  display: inline-block;
  transition: 0.3s ease;
}

.header__sublist {
  position: absolute;
  top: 100%;
  left: rem(-22);
  width: calc(100% + rem(44));
  height: auto;
  z-index: 10;
  background: #f3f3f3;
  padding: rem(20) rem(16) rem(20) rem(40);
  opacity: 0;
  visibility: hidden;
  transform: translateY(rem(10));
  transition: all 0.3s ease;
}

.header__sublist::before {
  content: "";
  position: absolute;
  top: rem(20);
  bottom: rem(20);
  left: rem(12);
  width: rem(2);
  background-color: #e3e3e3;
}

.header__subitem:not(:first-child) {
  margin-top: rem(10);
}

.header__sub-link {
  display: block;
  color: $black;
  font-size: rem(12);
  white-space: nowrap;
  text-decoration: none;
  transition: color 0.3s ease;
}

.header__sub-link:hover {
  color: $gold;
  text-decoration-line: underline;
}

.header__buttons {
  display: none;
  @media (max-width: 1240px) {
    display: flex;
    align-items: center;
    gap: rem(12);
    height: inherit;
  }

  @media (max-width: 780px) {
    display: none;
  }
}

.header__menu-button {
  display: none;

  @media (max-width: 1240px) {
    display: flex;
    font-size: rem(14);
    color: $black;
    padding: rem(6) rem(12);
    background: transparent;
    align-items: center;
    gap: rem(6);
    transition: color 0.3s ease;
    border: none;
    cursor: pointer;
    height: inherit;
  }

  @media (max-width: 780px) {
    display: none;
  }
}

.header__menu-button:hover {
  color: $gold;
}

.header__menu-text {
  font-size: rem(14);
  font-family: $second-font-family;
  font-weight: $medium;
  display: inline-block;
}

.header__menu-icon {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  width: rem(18);
  height: rem(14);
  margin-left: rem(8);
}

.header__line {
  display: block;
  height: rem(2);
  background-color: $gold;
  border-radius: rem(1);
  width: 100%;
  transition: all 0.3s ease;
}
