@use "global" as *;

.footer {
  padding: rem(120) rem(80);
  position: relative;
  overflow: hidden;
  background: linear-gradient(128deg, #2c2826 73.85%, #b99568 144.74%);
  @include mq("md") {
    padding: vw(780, 160) vw(780, 48);
    background: linear-gradient(128deg, #2c2826 39.88%, #b99568 144.74%);
  }
}

.footer__inner {
  margin: 0 auto;
}

.footer__logo {
  max-width: rem(145);
  width: 100%;
  height: inherit;
  z-index: 1;
  @include mq("md") {
    position: absolute;
    max-width: vw(780, 288);
    bottom: vw(780, 248);
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: vw(780, 32);
  }
}

.footer__logo img {
  width: 100%;
  aspect-ratio: 146/32;
  object-fit: cover;
  flex-shrink: 0;
}

.footer__logo-text {
  font-size: rem(12);
  font-weight: $medium;
  line-height: 1.3;
  font-family: $second-font-family;
  color: $white;
  letter-spacing: 0.05em;
  white-space: nowrap;

  @include mq("md") {
    font-size: vw(780, 30);
  }
}

.footer__nav {
  display: grid;
  grid-template-columns: auto 2fr 2fr 1fr;
  gap: rem(40);

  @media (max-width: 1240px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: rem(24);
  }

  @include mq("md") {
    grid-template-columns: 1fr;
    gap: vw(780, 80);
  }
}

.footer__nav-block {
  display: flex;
  gap: rem(30);

  @media (max-width: 1240px) {
    display: flex;
    flex-direction: column;
    gap: rem(30);
  }
  @include mq("md") {
    gap: vw(780, 80);
  }
}

.footer__nav-list {
  display: flex;
  flex-direction: column;
  border-top: 1px solid $brown;
  padding-top: rem(16);
  flex: 1;
  @include mq("md") {
    padding-top: vw(780, 33);
  }
}

.footer__nav-item {
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.8;
  letter-spacing: 0.03em;
  color: $white;
  @include mq("md") {
    font-size: vw(780, 32);
  }
}

.footer__nav-item:not(:first-child) {
  border-top: 1px solid $brown;
  padding-top: rem(16);
  margin-top: rem(30);
  @include mq("md") {
    padding-top: vw(780, 33);
    margin-top: vw(780, 80);
  }
}

.footer__nav-item a {
  transition: all 0.3s ease;
}

.footer__nav-item a:hover {
  opacity: 0.5;
}

.footer__nav-subitem {
  margin-top: rem(10);
  font-size: rem(12);
  color: $gray;
  font-weight: $normal;

  @include mq("md") {
    font-size: vw(780, 24);
    margin-top: vw(780, 24);
  }
}

.footer__nav-subitem a {
  transition: all 0.3s ease;
}

.footer__nav-subitem a:hover {
  color: #635d58;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: rem(210);
  @include mq("md") {
    flex-direction: column;
    margin-top: vw(780, 128);
    gap: rem(20);
  }
}

.footer__link-group {
  display: flex;
  gap: rem(20);

  @include mq("md") {
    flex-direction: column;
    align-items: flex-start;
    gap: vw(780, 28);
  }
}

.footer__link {
  position: relative;
  font-size: rem(12);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.03em;
  color: $gray;
  padding-left: rem(20);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  @include mq("md") {
    padding-left: vw(780, 36);
    font-size: vw(780, 24);
  }
}

.footer__link::before {
  content: "";
  position: absolute;
  width: rem(16);
  height: rem(16);
  left: 0;
  background-image: url(../images/common/icon_blank.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  @include mq("md") {
    width: vw(780, 32);
    height: vw(780, 32);
  }
}

.footer__link:hover {
  opacity: 0.5;
}

.footer__copyright {
  font-size: rem(10);
  font-weight: $normal;
  letter-spacing: 0.05em;
  line-height: 1.5;
  color: $gray;
  @include mq("md") {
    text-align: center;
    margin-top: vw(780, 248);
    font-size: vw(780, 20);
  }
}

.footer__line {
  position: absolute;
  width: rem(934);
  height: rem(934);
  bottom: rem(-324);
  right: rem(243);
  pointer-events: none;
  transform: translate(50%, 50%) rotate(45deg);
  @include mq("md") {
    width: vw(780, 1550);
    height: vw(780, 1550);
    bottom: vw(780, -375);
    right: vw(780, 115);
  }
}

.footer__line svg {
  width: 100%;
  height: 100%;
}

.footer__line svg path {
  fill: none;
  stroke: #635d58;
  stroke-width: 1.3;
  stroke-dasharray: 934;
  stroke-dashoffset: 934;
  transition: stroke-dashoffset 0.8s ease-out;
  @include mq("md") {
  }
}

.footer__line svg .footer__line-left {
  transition-delay: 0s;
}

.footer__line svg .footer__line-top {
  transition-delay: 0.8s;
}

.footer__line.is-animate svg path {
  stroke-dashoffset: 0;
}
