@use "global" as *;

.zoom-in {
  overflow: hidden;
  position: relative;
}
.zoom-in img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -webkit-filter: blur(rem(8));
  filter: blur(rem(8));
  opacity: 0;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.zoom-in.is-show img {
  -webkit-filter: blur(0);
  filter: blur(0);
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: opacity 0.1s linear, -webkit-filter 0.6s linear,
    -webkit-transform 1s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 0.1s linear, -webkit-filter 0.6s linear,
    -webkit-transform 1s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.6s linear, opacity 0.1s linear,
    transform 1s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.6s linear, opacity 0.1s linear,
    transform 1s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1),
    -webkit-filter 0.6s linear,
    -webkit-transform 1s 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
