@use "global" as *;

.business {
  overflow-x: hidden;
}

.business__inner {
  padding-left: rem(80);
  @include mq("md") {
    padding-inline: vw(780, 45);
  }
}

.business__content {
  display: flex;
  gap: rem(40);
  justify-content: space-between;
  @include mq("md") {
    flex-direction: column;
    gap: 0;
  }
}

.business__content-left {
  min-width: 0;
  max-width: rem(510);
  @include mq("md") {
    max-width: 100%;
  }
}

.business__text {
  margin-top: rem(32);
  line-height: 1.7;
  letter-spacing: 0.05em;
  @include mq("md") {
    margin-top: vw(780, 37);
    font-size: vw(780, 24);
  }
}

.business__link {
  margin-top: rem(42);
  @include mq("md") {
    margin-top: vw(780, 52);
  }
}

.business__gallery {
  width: calc(((100% + rem(80)) - rem(40)) / 2);
  height: rem(414);
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
  @include mq("md") {
    margin-top: vw(780, 96);
    height: vw(780, 432);
    width: 100vw;
    margin-right: calc(50% - 50vw);
  }
}

.business__gallery-image {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.business__gallery-scene {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  transition: opacity 0.5s ease;
}

.business__gallery-scene img {
  aspect-ratio: 350 / 207;
  object-fit: cover;
  width: 100%;
}

// 最初の画像のみ初期表示
.business__gallery-scene--1 {
  opacity: 1;
}

// 最初の画像用（アニメーションなし）
.business__gallery-scene.is-initial {
  opacity: 1;
  z-index: 3;
}

// 2回目以降の切り替え用（アニメーションあり）
// 最初の画像に戻る場合もこちらを使用
.business__gallery-scene.is-active {
  opacity: 1;
  z-index: 3;
  animation: business-mask-animation 0.8s linear;
}

.business__gallery-scene.is-previous {
  opacity: 1;
  z-index: 2;
}

.business__gallery-scene img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

// main-mvと同じページめくり効果のキーフレーム
@keyframes business-mask-animation {
  0% {
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  }
  75% {
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 30% 100%);
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}
