@use "global" as *;

.people__wrap {
  overflow: hidden;
}

.people__content {
  padding-inline: rem(80);
  @include mq("md") {
    padding-inline: vw(780, 48);
  }
}

.people__text {
  margin-top: rem(40);
  letter-spacing: 0.05em;
  line-height: 1.7;
  @include mq("md") {
    margin-top: rem(20);
    font-size: vw(780, 26);
  }
}

.people__arrow-link {
  margin-top: rem(40);
  @include mq("md") {
    margin-top: vw(780, 52);
  }
}

.people__slider {
  @include mq("md") {
    margin-top: vw(780, 96);
  }
}
