@use "global" as *;

.drawer {
  display: none;
  @media (max-width: 1240px) {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    width: rem(510);
    padding: rem(64) rem(48) rem(80);
    height: 100%;
    z-index: 1001;
    background-color: $white;
    transform: translateX(100%);
    transition: transform 0.3s ease;
    overflow-y: auto;
  }

  @include mq("md") {
    width: 100%;
    padding: vw(780, 68) vw(780, 48) vw(780, 256);
    top: 0;
    transform: translateX(0);
    transition: none;
    opacity: 0;
    visibility: hidden;
  }
}

.drawer.is-open {
  transform: translateX(0);
  @include mq("md") {
    opacity: 1;
    visibility: visible;
  }
}

.drawer__close {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: auto;
  gap: rem(8);
  background: none;
  border: none;
  font-size: rem(14);
  font-weight: 500;
  color: $black;
  transition: color 0.3s ease;
  padding: rem(10) rem(20) rem(10) 0;
  @include mq("md") {
    display: none;
  }
}

.drawer__close:hover {
  color: $gold;
}

.drawer__close-text {
  font-size: rem(14);
  font-family: $second-font-family;
}

.drawer__close-icon {
  display: inline-block;
  position: relative;
  width: rem(16);
  height: rem(16);
  margin-left: rem(8);
}

.drawer__close-line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: rem(2);
  background-color: $gold;
  transform-origin: center;
}

.drawer__close-line:first-child {
  transform: rotate(45deg);
}

.drawer__close-line:last-child {
  transform: rotate(-45deg);
}

.drawer__nav {
  margin-top: rem(60);
  @include mq("md") {
    margin-top: 0;
  }
}

.drawer__logo {
  display: none;
  @include mq("md") {
    height: inherit;
    display: flex;
    align-items: center;
    gap: vw(780, 32);
  }
}

.drawer__logo-img {
  @include mq("md") {
    max-width: vw(780, 216);
    width: 100%;
  }
}

.drawer__logo-img img {
  @include mq("md") {
    width: 100%;
    aspect-ratio: 146/32;
    object-fit: cover;
    flex-shrink: 0;
  }
}

.drawer__logo-text {
  @include mq("md") {
    font-size: vw(780, 22);
    font-weight: 500;
    line-height: 1.2;
    color: $black;
    white-space: nowrap;
  }
}

.drawer__list {
  @include mq("md") {
    margin-top: vw(780, 128);
  }
}

.drawer__item {
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.05em;
  color: $black;
  display: block;
  border-top: 1px solid $gray;
  position: relative;
}

.drawer__item.drawer__item--gold {
  color: $gold;
}

.drawer__item.drawer__item--margin {
  margin-top: rem(30);
  @include mq("md") {
    margin-top: vw(780, 78);
  }
}

.drawer__link {
  display: block;
  font-size: rem(16);
  padding-top: rem(16);
  padding-bottom: rem(40);
  text-decoration-line: underline;
  transition: all 0.3s ease;
  @include mq("md") {
    font-size: vw(780, 32);
    padding-top: vw(780, 31);
    padding-bottom: vw(780, 78);
  }
}

.drawer__link:hover {
  color: $gold;
}

.drawer__item--gold .drawer__link {
  text-decoration-line: none;
}

.drawer__item-title {
  padding-top: rem(16);
  position: relative;
  display: inline-block;
  @include mq("md") {
    padding-top: vw(780, 32);
    font-size: vw(780, 32);
  }
}

.drawer__item-title.drawer__item-title--margin {
  padding-bottom: rem(50);
}

.drawer__item.drawer__item--external-link {
  margin-top: rem(24);
  border: none;
  font-size: rem(14);
  @include mq("md") {
    margin-top: 0;
    font-size: vw(780, 24);
  }
}

.drawer__sublist {
  display: flex;
  flex-direction: column;
  margin-top: rem(24);
  @include mq("md") {
  }
}

.drawer__subitem {
  color: $brown;
  font-weight: $medium;

  @include mq("md") {
  }
}

.drawer__subitem.drawer__subitem--margin {
  margin-top: rem(16);
  @include mq("md") {
    margin-top: vw(780, 32);
  }
}

.drawer__subitem-link {
  padding: rem(8) 0;
  text-decoration: underline;
  transition: all 0.3s ease;
  display: block;
  font-size: rem(12);
  @include mq("md") {
    padding: vw(780, 12) 0;
    font-size: vw(780, 24);
  }
}

.drawer__external-link {
  position: relative;
  font-size: rem(12);
  padding-left: rem(20);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  @include mq("md") {
    font-size: vw(780, 24);
    padding-left: vw(780, 36);
  }
}

.drawer__external-link:before {
  content: "";
  position: absolute;
  width: rem(16);
  height: rem(16);
  left: 0;
  background-color: $brown;
  mask-image: url(../images/common/icon_blank.svg);
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  @include mq("md") {
    width: vw(780, 32);
    height: vw(780, 32);
  }
}

.drawer__subitem-link:hover {
  color: $gold;
}

.drawer__external-link:hover {
  color: $gold;
}

.drawer__external-link:hover:before {
  background-color: $gold;
}

.drawer__entry {
  margin-top: rem(64);
  @include mq("md") {
    display: none;
  }
}

// ========================================
// ドロワーリスト順次表示アニメーション
// ========================================

// 初期状態：すべてのメニューアイテムを非表示
.drawer__link,
.drawer__item-title,
.drawer__subitem-link,
.drawer__external-link {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94),
    transform 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

// アクティブ状態：メニューが開いた時の基本スタイル
.drawer.is-open .drawer__link,
.drawer.is-open .drawer__item-title,
.drawer.is-open .drawer__subitem-link,
.drawer.is-open .drawer__external-link {
  opacity: 1;
  transform: translateY(0);
}

// 1番目のメニュー（トップページ）
.drawer.is-open .drawer__item:nth-child(1) .drawer__link {
  transition-delay: 0.1s;
}

// 2番目のメニュー（メッセージ）
.drawer.is-open .drawer__item:nth-child(2) .drawer__link {
  transition-delay: 0.14s;
}

// 3番目のメニュー（事業紹介）
.drawer.is-open .drawer__item:nth-child(3) .drawer__link {
  transition-delay: 0.18s;
}

// 4番目のメニュー（働く人々）
.drawer.is-open .drawer__item:nth-child(4) .drawer__link {
  transition-delay: 0.22s;
}

// 5番目のメニュー（スペシャルコンテンツ）
.drawer.is-open .drawer__item:nth-child(5) .drawer__item-title {
  transition-delay: 0.26s;
}

// スペシャルコンテンツのサブメニュー
.drawer.is-open
  .drawer__item:nth-child(5)
  .drawer__subitem:nth-child(1)
  .drawer__subitem-link {
  transition-delay: 0.3s;
}

.drawer.is-open
  .drawer__item:nth-child(5)
  .drawer__subitem:nth-child(2)
  .drawer__subitem-link {
  transition-delay: 0.34s;
}

.drawer.is-open
  .drawer__item:nth-child(5)
  .drawer__subitem:nth-child(3)
  .drawer__subitem-link {
  transition-delay: 0.38s;
}

.drawer.is-open
  .drawer__item:nth-child(5)
  .drawer__subitem:nth-child(4)
  .drawer__subitem-link {
  transition-delay: 0.42s;
}

// 6番目のメニュー（働く環境・カルチャー）
.drawer.is-open .drawer__item:nth-child(6) .drawer__item-title {
  transition-delay: 0.46s;
}

// 働く環境・カルチャーのサブメニュー
.drawer.is-open
  .drawer__item:nth-child(6)
  .drawer__subitem:nth-child(1)
  .drawer__subitem-link {
  transition-delay: 0.5s;
}

.drawer.is-open
  .drawer__item:nth-child(6)
  .drawer__subitem:nth-child(2)
  .drawer__subitem-link {
  transition-delay: 0.54s;
}

// 7番目のメニュー（募集要項・選考フロー）
.drawer.is-open .drawer__item:nth-child(7) .drawer__link {
  transition-delay: 0.58s;
}

// 外部リンクグループ
.drawer.is-open .drawer__external-link:nth-child(1) {
  transition-delay: 0.62s;
}

.drawer.is-open .drawer__external-link:nth-child(2) {
  transition-delay: 0.66s;
}
