@use "global" as *;

.sub-business__inner {
  padding: 0 rem(80);
  @include mq("md") {
    padding: 0 vw(780, 48);
  }
}

.sub-business__text {
  margin-top: rem(96);
  margin-left: auto;
  max-width: rem(620);
  @include mq("md") {
    margin-top: vw(780, 160);
    margin-left: 0;
    font-size: vw(780, 26);
    max-width: 100%;
  }
}
